* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

select {
  width: 100%;
  height: 30px;
  text-align: center;
  background-color: rgba(255, 211, 211, .275);
  border-radius: 50px;
  font-size: 20px;
}

.container {
  max-width: 1000px;
  background-color: #e5cbeb;
  border-radius: 20px;
  padding: 50px;
}

.container:hover {
  background-color: rgba(0, 195, 255, .173);
  transition: background-color 1.5s, box-shadow 1.5s;
}

.cat {
  flex-wrap: wrap;
  justify-content: center;
  display: flex;
}

.info {
  max-width: 500px;
  margin-top: 10px;
  margin-left: 30px;
}

.subtitle {
  font-weight: bold;
}

.error, .loader {
  background-color: red;
  border-radius: 50px;
  padding: 10px;
  font-size: 20px;
  font-weight: bold;
}

.loader {
  background-color: #7f81ff;
}

.hidden {
  display: none;
}

